
.root {
  position: relative;
  z-index: 1;
  padding-bottom: 7.5rem;
}
.root:last-child {
  padding-bottom: 6.25rem;
}
@media print {
.root {
    padding: 0.3cm 1cm 0.5cm;
}
.root .title {
    orphans: 3;
}
:global(#contacts) .root {
    display: flex;
    flex-direction: column;
    align-items: center;
}
}
.title {
  margin-bottom: 2em;
  text-align: center;
}
@media (min-width: 64em) {
.title {
    backface-visibility: hidden;
    transform-style: preserve-3d;
    transform: translate3d(0, 0, 0);
    will-change: transform;
    position: absolute;
    top: 0.625rem;
    left: 0;
    max-width: 35%;
    margin-bottom: 0;
    text-align: left;
    white-space: nowrap;
}
}
@media (min-width: 80em) {
.title {
    left: 4.6875vw;
}
}
@media print {
.title {
    position: static;
    max-width: none;
    margin-top: 1.3em;
    margin-bottom: 1.5em;
    padding-top: 0.5cm;
    text-align: center;
}
}
.body {
  display: block;
  width: 100%;
}
@media (max-width: 63.99em) {
.body {
    transform: none !important;
}
}
@media (min-width: 48em) {
.body {
    padding-right: 7%;
    padding-left: 10%;
}
}
@media (min-width: 64em) {
.body {
    padding-right: 0;
    padding-left: 33%;
    will-change: transform;
    backface-visibility: hidden;
}
}
@media print {
.body {
    padding: 0;
    transform: none !important;
}
:global(#contacts) .body {
    width: auto;
    white-space: nowrap;
}
}




